        // //         }
        // //     });
        // //     setTimeout(function () {
        // //         vm.lgId = 0;
        // //         setTimeout(function () {
        // //             if (vm.chatTab != true) {
        // //                 vm.$refs.ChatsTab.classList.remove("active");
        // //             }
        // //         }, 10);
        // //     }, 6000);

        // // } else if (vm.$refs.ChatsTab.classList.contains('active') == true) {
        // //     vm.blinkUserId = vm.sender.id;
        // //     vm.isUserLoggedInId.forEach((itm) => {
        // //         if (itm != vm.blinkUserId && vm.chatUserInfo.GROUP_ID != data.roomId) {
        // //             var element = document.getElementById(userLoggedIn.GROUP_ID);
        // //             // element.classList.remove('active');
        // //             element.classList.add('blinkingcommon2');
        // //             setTimeout(function () {
        // //                 var element = document.getElementById(userLoggedIn.GROUP_ID);
        // //                 element.classList.remove('blinkingcommon2');
        // //             }, 6000);

        // //         }
