        //     let vm = this;
        //     const chatUserId = vm.chatUsersList.find(x => x.GROUP_ID == room)
        //     // var chatUserId = $("#div-left-forChatDiv .contacts-panel #" + room).attr("data-receiver");
        //     this.$socket.emit('get Open Chat User Status', chatUserId);
        // },
