         async JoinRooms(itm) {
            debugger
            var vm = this;
           // vm.$refs.chatTab.isDataLoaded = true;
            vm.chatHistoryDetails = [];

            vm.$socket.emit('is typing', {
                user: vm.sender.id,
                Group: itm.GROUP_ID,
                typing: false,
                IsGroup: itm.IS_GROUP
            })
            var paramsObj = {
                alias: itm.GROUP_NAME_ALIAS,
                companyId: vm.sender.companyId,
                emaiId: vm.sender.emaiId,
                groupName: itm.GROUP_NAME,
                id: vm.sender.id,
                imgPath: vm.sender.imgPath,
                is_first_time: 1,
                isGlobalSearch: false,
                lastchatId: null,
                lastrow: 0,
                name: vm.sender.name,
                room: itm.GROUP_NAME_ALIAS
            };
            await vm.$socket.emit('joinRoom', paramsObj,itm);
            itm.isActive == true;
            if (Object.prototype.hasOwnProperty.call(itm, 'GROUP_ID')) {
                var grpName = vm.ChatUserListData.find(x => x.GROUP_ID == itm.GROUP_ID)
                vm.RecieverId = itm.RECEIVER_ID;
                // if(vm.sender.id==itm.RECEIVER_ID)
                // {
                vm.groupNameDisplay = itm.GROUP_NAME ? itm.GROUP_NAME : grpName.GROUP_NAME;
                vm.userGroupId = itm.GROUP_ID;
                // }
                vm.GroupAliasName = itm.GROUP_NAME_ALIAS ? itm.GROUP_NAME_ALIAS : grpName.GROUP_NAME_ALIAS;
                vm.chatUserInfo = itm;
                vm.Groupdata = itm;
            }
            // vm.isDataLoaded=false;
            if (itm.FROM_USER_ID == vm.sender.id) {
                vm.closeGroup()
                await vm.$refs.chatTab.backToChat();
                vm.$refs.chatTab.forwardCancel();
            }
            vm.readMessageUpdate(itm.GROUP_ID, vm.sender);
            //vm.$socket.emit('get All Chat Persons', vm.sender);
            // let myObj = {
            //     companyId: vm.store2.GetUserInfo.CompanyId,
            //     userId: vm.store2.GetUserInfo.ID,
            //     emaiId: vm.store2.GetUserInfo.Email,
            //     name: vm.store2.GetUserInfo.Name,
            //     imgPath: vm.blobUserUrl + vm.store2.GetUserInfo.Avatar,
            //     type: vm.type
            // }
            // vm.$socket.emit('user total', vm.sender);
            // if(lbl !='fromGlobalSearch')
            // {
            //     setTimeout(function () {
            //             vm.$refs.chatTab.ScrollBottomChat();
            //     }, 1200);
            // }
            vm.$parent.chatOpen()

        },
        readMessageUpdate(room, user) {
            debugger
            var param = new Object();
            param = user;
            param.id = this.sender.id;
            param.room = room;
            param.email = this.sender.emaiId;
            this.$socket.emit('user chat Read', param);
        },
        DateDifference(ActualDate) {
            let vm = this;
            let GetCurrentDate = new Date();
            let GetValueInSeconds = Math.ceil(Math.floor(GetCurrentDate.getTime() - new Date(ActualDate).getTime()) / 1000);
            // TO Get Hours
            let hours = Math.floor(GetValueInSeconds / (60 * 60));
            // To Get Minutes
            let divisor_for_minutes = GetValueInSeconds % (60 * 60);
            let minutes = Math.floor(divisor_for_minutes / 60);
            let days = Math.floor(GetValueInSeconds / (3600 * 24));
            if (GetValueInSeconds < 60) {
                return vm.$t('now');
            } else if (GetValueInSeconds < 120) {
                return vm.$t('m');
            } else if (GetValueInSeconds < 2700) {
                return minutes + ' ' + vm.$t('m');
            } else if (GetValueInSeconds < 5400) {
                return vm.$t('an') + ' ' + vm.$t('h');
            } else if (GetValueInSeconds < 86400) {
                return hours + ' ' + vm.$t('h');
            } else if (GetValueInSeconds < 172800) {
                return vm.$t('Y');
            } else if (GetValueInSeconds < 2592000) {
                return days + ' ' + vm.$t('d');
            } else if (GetValueInSeconds < 31104000) {
                let months = Math.floor(days / 30);
                return months <= 1 ? '1 ' + vm.$t('mo') : months + ' ' + vm.$t('mo')
            } else {
                let year = Math.floor(days / 365);
                return year <= 1 ? '1 ' + vm.$t('y') : year + ' ' + vm.$t('y');
            }
        },

    },
    setup() {

        return {

        }
    }
}
</script>
